@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,900&display=swap);
:root {
  --roboto: 'Roboto', sans-serif;
  --roboto-mono: 'Roboto Mono', monospace;
  --roboto-slab: 'Roboto Slab', serif;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-family: var(--roboto);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  min-height: 100vh;
  background-color: #f2f2f2;
  position: relative;
  /* background-color: rgba(222, 236, 241, 1); */
}

body:hover {
  cursor: url(/g-is-for-giuliana/static/media/mouse-pointer.4af98ea7.svg), auto;
}

h1,
h2,
h3 {
  font-family: inherit;
  -webkit-font-feature-settings: "c2sc";
          font-feature-settings: "c2sc";
  font-variant: small-caps;
}

h1 {
  font-weight: 900;
}

h2 {
  font-weight: 700;
  font-size: 1.25em;
}

:root {
  --extra-deep-red: #ae2f38;
  --deep-red: #cd3943;
  --bright-red: #e64250;
  --pink: #ec5569;
  --aquamarine: #a8ebfa;
  --turquoise: #76e2f8;
  --very-light-blue: #deecf1;
  --light-blue: #c6e3e7;
  --mid-blue: #8ebac5;
  --bright-blue: #0893fb;
  --grayish-blue: #3f5958;
  --slate: #384949;
  --dark-slate: #293939;
  --almost-black: #293939;
  --bright-green: #93c017;
  --light-green: #a8d32a;
  --yellow: #fcc038;
  --orange: #ffa03a;
}

.card-border {
  width: 336px;
  height: 192px;
  display: flex;
  justify-content: center;
  padding: 10px;
  border-radius: 3px;
  background: #76e2f8;
  background: var(--turquoise);
}

.front-animation {
  background: -webkit-linear-gradient(
    -45deg,
    #cd3943 25%,
    #76e2f8 25%,
    #76e2f8 50%,
    #cd3943 50%,
    #cd3943 75%,
    #76e2f8 75%
  );
  background: -webkit-linear-gradient(
    -45deg,
    var(--deep-red) 25%,
    var(--turquoise) 25%,
    var(--turquoise) 50%,
    var(--deep-red) 50%,
    var(--deep-red) 75%,
    var(--turquoise) 75%
  );
  background: -webkit-linear-gradient(
    135deg,
    #cd3943 25%,
    #76e2f8 25%,
    #76e2f8 50%,
    #cd3943 50%,
    #cd3943 75%,
    #76e2f8 75%
  );
  background: linear-gradient(
    -45deg,
    #cd3943 25%,
    #76e2f8 25%,
    #76e2f8 50%,
    #cd3943 50%,
    #cd3943 75%,
    #76e2f8 75%
  );
  background: -webkit-linear-gradient(
    135deg,
    var(--deep-red) 25%,
    var(--turquoise) 25%,
    var(--turquoise) 50%,
    var(--deep-red) 50%,
    var(--deep-red) 75%,
    var(--turquoise) 75%
  );
  background: linear-gradient(
    -45deg,
    var(--deep-red) 25%,
    var(--turquoise) 25%,
    var(--turquoise) 50%,
    var(--deep-red) 50%,
    var(--deep-red) 75%,
    var(--turquoise) 75%
  );
  background-size: 84px 84px;
  /* background-size: 168px 168px; */
  -webkit-transition: width 300ms ease-in-out;
  transition: width 300ms ease-in-out;
  -webkit-transition: height 300ms ease-in-out;
  transition: height 300ms ease-in-out;
  -webkit-animation: barberpole-front 3s infinite linear;
  animation: barberpole-front 3s infinite linear;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  will-change: background-position;
}

.back-animation {
  background: -webkit-linear-gradient(
    225deg,
    #cd3943 25%,
    #76e2f8 25%,
    #76e2f8 50%,
    #cd3943 50%,
    #cd3943 75%,
    #76e2f8 75%
  );
  background: -webkit-linear-gradient(
    225deg,
    var(--deep-red) 25%,
    var(--turquoise) 25%,
    var(--turquoise) 50%,
    var(--deep-red) 50%,
    var(--deep-red) 75%,
    var(--turquoise) 75%
  );
  background: linear-gradient(
    225deg,
    #cd3943 25%,
    #76e2f8 25%,
    #76e2f8 50%,
    #cd3943 50%,
    #cd3943 75%,
    #76e2f8 75%
  );
  background: linear-gradient(
    225deg,
    var(--deep-red) 25%,
    var(--turquoise) 25%,
    var(--turquoise) 50%,
    var(--deep-red) 50%,
    var(--deep-red) 75%,
    var(--turquoise) 75%
  ); background-size: 84px 84px;
  /* background-size: 168px 168px; */
  -webkit-transition: width 300ms ease-in-out;
  transition: width 300ms ease-in-out;
  -webkit-transition: height 300ms ease-in-out;
  transition: height 300ms ease-in-out;
  -webkit-animation: barberpole-back 3s infinite linear;
  animation: barberpole-back 3s infinite linear;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  will-change: background-position;
}

@-webkit-keyframes barberpole-front {
  from {
    background-position: 0%;
  }
  to {
    background-position: 100%;
  }
}

@keyframes barberpole-front {
  from {
    background-position: 0%;
  }
  to {
    background-position: 100%;
  }
}

@-webkit-keyframes barberpole-back {
  from {
    background-position: 100%;
  }
  to {
    background-position: 0%;
  }
}

@keyframes barberpole-back {
  from {
    background-position: 100%;
  }
  to {
    background-position: 0%;
  }
}

.card-container {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  align-content: center;
  background-color: #ffffff;
  border: 1px solid transparent;
  border-radius: 3px;
}

.card-contents {
  flex-grow: 1;
  border-radius: 3px;
  text-align: center;
}

.icons {
  -webkit-perspective: 100vw;
          perspective: 100vw;
  margin: 1.5em auto;
  height: auto;
  max-width: 250px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.icons:hover,
.card-border.card-side--back:hover,
.card-border.card-side--front:hover {
  cursor: url(/g-is-for-giuliana/static/media/mouse-pointer.4af98ea7.svg), default;
}

.icons,
.icons img {
  -webkit-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
}

a:hover,
a:focus,
a:active {
  opacity: 0.5;
  cursor: pointer;
}

a:focus {
  outline: none;
}

.card-contents > *:first-child {
  margin-top: 0;
}

.card-contents > *:last-child {
  margin-bottom: 0;
}

.card {
  -webkit-perspective: 100vw;
  perspective: 100vw;
  width: 336px;
  height: 192px;
}

.card-side {
  width: 336px;
  height: 192px;
  -webkit-transition: all 0.8s ease-in-out;
  transition: all 0.8s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
  margin: auto;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 3px;
  overflow: hidden;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-perspective-origin: center;
          perspective-origin: center;
  background-color: #76e2f8;
  background-color: var(--turquoise);
  box-shadow: 0 1.5rem 5rem rgba(0, 0, 0, 0.3);
}

.card-side--back {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

.card:hover .card-side--front {
  -webkit-transform: rotateY(-180deg);
  transform: rotateY(-180deg);
}

.card:hover .card-side--back {
  -webkit-transform: rotateY(0deg);
  transform: rotateY(0deg);
}

.no-hover .card .card-side--front {
  -webkit-transform: rotateY(-180deg);
  transform: rotateY(0deg);
}

.no-hover .card .card-side--back {
  -webkit-transform: rotateY(0deg);
  transform: rotateY(180deg);
}

.no-hover .card.flipped .card-side--front {
  -webkit-transform: rotateY(-180deg);
  transform: rotateY(-180deg);
}

.no-hover .card.flipped .card-side--back {
  -webkit-transform: rotateY(0deg);
  transform: rotateY(0deg);
}

@media (max-width: 355px) {
  .card-border {
    height: 336px;
    width: 192px;
    background-size: 48px 48px;
  }

  .card {
    height: 336px;
    width: 192px;
  }

  .card-side {
    height: 336px;
    width: 192px;
  }

  .card-contents {
    text-align: center;
  }

  h2 {
    width: 120px;
    margin-right: auto;
    margin-left: auto;
  }
}

@media (min-width: 355.98px) {
  h1,
  h2,
  h3 {
    margin: 0;
  }
}

